@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;700&display=swap);
*{
    margin:0;
    padding:0;
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
}

body{
    background-color: rgb(231, 231, 231);
}

.corpo{
    width:60%;
    margin: auto;
    padding-top: 3em;
}

.corpo h3{
    color: rgba(222,142,133,255);
    padding-bottom: 10px;
}

.corpo p{
    line-height: 150%;
    font-weight: 600;
    font-size: 14px;
    color: rgb(115, 115, 115);
}

@media screen and (max-width: 800px) {
    .corpo{
        width:90%;
    }
}
.navbar{
    width:100%;
    height: 8vh;
    background-color: rgba(19,60,141,255);
    display: flex;
    position: fixed;
    z-index: 100;
}

.navbar-container{
    width:60%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    margin: auto;
}

.categorias{
    display: flex;
    margin:auto 1em;
    color: white;
    cursor: pointer;
}

.categorias i{
    margin: auto;
    font-size: 15px;
}

.categorias h3{
    padding-left: 0.5em;
    font-size: 13px;
    margin: auto;
}

.busca{
    height: 70%;
    width: 50%;
    margin:auto 1em;
    display: flex;
    position: relative;
}

.busca input{
    height: 100%;
    width: 100%;
    border-radius: 3px;
    border: none;
    padding: 0 1em;
    margin:auto;
}

.busca i{
    color: rgb(170, 170, 170);
    position: absolute;
    right: 10px;
    line-height: 5.4vh;
    cursor: pointer;
    font-size: 15px;
}

.entrar{
    display: flex;
    margin:auto 1em;
    color: white;
    cursor: pointer;
}

.entrar i{
    margin: auto;
    font-size: 15px;
}

.entrar h3{
    padding-left: 0.5em;
    font-size: 13px;
    margin: auto;
}

.carrinho{
    display: flex;
    margin:auto 1em;
    color: white;
    cursor: pointer;
}

.carrinho i{
    margin: auto;
    color: rgb(0, 198, 233);
    font-size: 15px;
}

.menu-mobile{
    display: none;
}

@media screen and (max-width: 800px) {
    .entrar{
        display: none;
    }
    .categorias{
        display: none;
    }
    .navbar-container{
        width:95%;
    }
    .busca {
        display: none;
    }
    .menu-mobile{
        margin:auto 1em;
        display: block;
        color: white;
        cursor: pointer;
        font-size: 25px;
    }
    .carrinho i{
        font-size: 25px;
    }
}
.hero{
    height: 50vh;
    width: 100%;
    background-color: rgba(222,142,133,255);
    padding-top: 8vh;
    display: flex;
}

.hero-imagem{
    width: 50%;
    overflow: hidden;
    background-position: right bottom;
}

.hero-texto{
    width: 50%;
    top:50%;
    color: white;
    padding: 5.5em 8em 0 4em;
}

.hero-texto h1:first-child{
    font-weight: 400;
}

.hero-texto h1{
    line-height: 100%;
}

.hero-texto p{
    line-height: 160%;
    padding-top: 0.5em;
    font-weight: 600;
}

@media screen and (max-width: 900px) {
    .hero-imagem{
        width: 0%;
    }
    .hero-texto{
        padding: 3.5em 1.5em;
        width: 100%;
    }
    .hero-texto h1{
        font-size: 35px;
    }

    .hero-texto p{
        font-size: 15px;
    }
  }
.filters-container{
    display: flex;
    width: 60%;
    margin: auto;
    justify-content: flex-end;
}

.filtros{
    display:flex;
    width: 205px;
    height: 37px;
    background-color: white;
    margin: 20px 5px;
    color: rgb(150, 150, 150);
    font-size: 13px;
    font-weight: 500;
    align-items: center;
    justify-content: center;
    cursor:pointer;
}

.filtros i, .filtros p{
    padding:0 3px;
}

.filtros i{
    font-size: 16px;
}


@media screen and (max-width: 800px) {
    .filters-container{
        width:90%;
        display: block;
    }

    .filtros{
        width: 100%;
    }
}
.cards-container{
    display: grid;
    grid-gap: 0em 20px;
	grid-template-columns:repeat(auto-fit, minmax(300px, 1fr));
    grid-auto-rows: -webkit-max-content;
    grid-auto-rows: max-content;
}

.cards-ul{
    list-style-type: none;
}

.card-item{
    background-color: white;
    margin: 20px 0 0 0;
    box-shadow: 2px 2px 2px rgb(201, 201, 201);
}

.wrapper{
    height: 100%;
    padding-bottom: 20px;
}

.card-image{
    background-color: white;
    margin: 20px 0 0 0;
    box-shadow: 2px 2px 2px rgb(201, 201, 201);
    height: 100%;
    overflow: hidden;
}

.card-header h3{
    padding: 0;
    margin: 0;
}

.card-header{
    padding: 1em;
}

.card-header-image h3{
    padding: 0;
    margin: 0;
    color: white;
}

.card-header-image h3 span{
    font-weight: 300;
}

.card-header-image{
    padding: 1em;
    background-color: rgba(222,142,133,255);
}

.card-item p{
    font-weight: 500;
    line-height: 160%;
    font-size: 14px;
    padding: 0 1em 1em 1em;
}

.card-body img{
    height: 100%;
}

.card-body {
    height: 100%;
}
.button{
    width: 100%;
    margin: 1em 0;
    line-height: 170%;
    font-size: 15px;
    font-weight: 300;
    padding: 7px;
    color: white;
    border: none;
    background-color: rgba(222,142,133,255);
    cursor: pointer;
}

.button span{
    font-weight: 700;
    text-decoration: underline;
}
